'use client';

import { useEffect } from 'react';
import { usePathname } from 'next/navigation';
import 'intro.js/introjs.css';

export default function Walkthrough() {
  const pathname = usePathname();

  useEffect(() => {
    if (typeof window === 'undefined') return;

    // Dynamic import intro.js
    import('intro.js').then((introJsModule) => {
      const introJs = introJsModule.default;
      
      // Check if user has completed walkthrough
      const hasCompletedWalkthrough = localStorage.getItem('walkthrough_completed');
      const walkthroughShown = sessionStorage.getItem(`walkthrough_${pathname}`);

      // Show walkthrough on first visit to each page
      if (!hasCompletedWalkthrough && !walkthroughShown) {
        setTimeout(() => {
          const steps = getStepsForPage(pathname);
          if (steps.length > 0) {
            const intro = introJs();
            
            // Set up event handlers using DOM events (intro.js v8 uses custom events)
            const handleComplete = () => {
              sessionStorage.setItem(`walkthrough_${pathname}`, 'true');
              document.removeEventListener('introjs:complete', handleComplete);
              document.removeEventListener('introjs:exit', handleExit);
            };
            
            const handleExit = () => {
              sessionStorage.setItem(`walkthrough_${pathname}`, 'true');
              document.removeEventListener('introjs:complete', handleComplete);
              document.removeEventListener('introjs:exit', handleExit);
            };
            
            // Listen for intro.js events before starting
            document.addEventListener('introjs:complete', handleComplete);
            document.addEventListener('introjs:exit', handleExit);
            
            intro.setOptions({
              steps,
              showProgress: true,
              showBullets: true,
              exitOnOverlayClick: true,
              exitOnEsc: true,
              nextLabel: 'Next →',
              prevLabel: '← Previous',
              skipLabel: 'Skip',
              doneLabel: 'Got it!',
            });
            
            intro.start();
          }
        }, 1000);
      }
    }).catch((error) => {
      console.warn('Failed to load intro.js:', error);
    });
  }, [pathname]);

  return null;
}

function getStepsForPage(pathname: string) {
  const steps: any[] = [];

  switch (pathname) {
    case '/':
      steps.push(
        {
          title: 'Welcome to Insight Agent IDP!',
          intro: 'This is your dashboard. Here you can see key metrics and quick actions.',
          element: 'h1',
        },
        {
          title: 'Quick Stats',
          intro: 'View your total transactions, spending, documents, and average transaction amounts at a glance.',
          element: '.grid',
        },
        {
          title: 'Quick Actions',
          intro: 'Use these shortcuts to upload documents, view analytics, or check for anomalies.',
          element: '.bg-white.shadow',
        }
      );
      break;

    case '/analytics':
      steps.push(
        {
          title: 'Analytics Dashboard',
          intro: 'Explore your spending trends, vendor statistics, and category breakdowns.',
          element: 'h1',
        },
        {
          title: 'Time Series Charts',
          intro: 'View monthly and daily spending trends to understand your expense patterns.',
          element: '.bg-white.shadow',
        },
        {
          title: 'Forecast',
          intro: 'See predicted spending for the next few months based on historical data.',
          element: '.bg-white.shadow',
        }
      );
      break;

    case '/documents':
      steps.push(
        {
          title: 'Document Management',
          intro: 'Upload, view, and manage all your documents here.',
          element: 'h1',
        },
        {
          title: 'Upload Documents',
          intro: 'Click here to upload new invoices or receipts. They will be automatically processed.',
          element: 'label[for="file-upload"]',
        },
        {
          title: 'Document List',
          intro: 'View all your processed documents. Click on any document to see extracted data and make corrections.',
          element: '.grid',
        }
      );
      break;

    case '/chat':
      steps.push(
        {
          title: 'Chat with Insight Agent',
          intro: 'Ask natural language questions about your documents and transactions.',
          element: 'h1',
        },
        {
          title: 'Ask Questions',
          intro: 'Try asking: "What are my top vendors?" or "Show me spending trends"',
          element: 'form',
        }
      );
      break;

    case '/anomalies':
      steps.push(
        {
          title: 'Anomaly Detection',
          intro: 'Automatically detect duplicates, unusual amounts, and missing fields in your documents.',
          element: 'h1',
        },
        {
          title: 'Anomaly List',
          intro: 'Review detected issues sorted by severity. High priority items need immediate attention.',
          element: '.space-y-4',
        }
      );
      break;
  }

  return steps;
}

export function startWalkthrough(steps: any[]) {
  if (typeof window === 'undefined') return;
  
  import('intro.js').then((module) => {
    const introJs = module.default;
    const intro = introJs();
    intro.setOptions({
      steps,
      showProgress: true,
      showBullets: true,
      exitOnOverlayClick: true,
      exitOnEsc: true,
      nextLabel: 'Next →',
      prevLabel: '← Previous',
      skipLabel: 'Skip',
      doneLabel: 'Got it!',
    });
    intro.start();
  }).catch((error) => {
    console.warn('Failed to load intro.js:', error);
  });
}

